@import url('https://fonts.googleapis.com/css2?family=Courgette&family=Lobster&family=Parisienne&family=Playfair+Display&family=Quintessential&family=Redressed&family=Roboto+Slab:wght@100;300&family=Supermercado+One&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-image: url(https://www.publicdomainpictures.net/pictures/210000/t2/orange-wood-texture-wallpaper.jpg);
  font-family: 'Roboto Slab', serif;

}
.backImage{
height: 100vh;
width: 100wh;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
}
.BorderBox{
  width: 50rem;
  height: 20rem;
  border: 1rem groove;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.btn-dark{
  width: 20rem;
  margin-top: 3rem;
}
/* sec component */
.ImgStyle{
  height: 21rem;
  width: 30rem;
}
.btn-fild{
  width: 40rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}
.btn{
margin: 1rem;
}
/* try again page */
.TryagainBox{
  width: 50rem;
  height: 20rem;
  border: 8px solid white;
  display: flex;
  margin-left: 16rem;
  margin-top: 6rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* MEDIA QUERY OF FIRST PAGE */
@media (max-width:768px) {
  .BorderBox{
    width: 40rem;
  }
}
@media (max-width:425px) {
  .BorderBox{
    width: 20rem;

  }
  .animalQGame{
    font-size: 1.5rem;
    text-align: center;
  }
  .EnterYname{
    font-size: 1.3rem;
  }
}

